body, .spinner {
  display: flex;
}

.spinner {
  position: fixed;
  color: #3df1f1;
  font: 1em Dosis, sans-serif;
  animation: tiltSpin 8s linear infinite;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  z-index: 1000;
}

.spinner, .spinner__ring {
  transform-style: preserve-3d;
}

.spinner__ring {
  animation-name: spin;
  animation-duration: 4s;
  animation-timing-function: inherit;
  animation-iteration-count: inherit;
  font-size: 1rem;
  position: relative;
  height: 2rem;
  width: 1rem;
}

.spinner__ring:nth-child(even) {
  animation-direction: reverse;
}

.spinner__sector {
  font-weight: 600;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  text-transform: uppercase;
  transform: translateZ(3rem);
}

.spinner__sector, .spinner__sector:empty:before {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.spinner__sector:empty:before {
  background: linear-gradient(transparent 45%, currentColor 45% 55%, transparent 55%);
  content: "";
}

.spinner__sector:nth-child(2) {
  transform: rotateY(12deg) translateZ(3rem);
}
.spinner__sector:nth-child(3) {
  transform: rotateY(24deg) translateZ(3rem);
}
.spinner__sector:nth-child(4) {
  transform: rotateY(36deg) translateZ(3rem);
}
.spinner__sector:nth-child(5) {
  transform: rotateY(48deg) translateZ(3rem);
}
.spinner__sector:nth-child(6) {
  transform: rotateY(60deg) translateZ(3rem);
}
.spinner__sector:nth-child(7) {
