.ring {
  border-radius: 50%;
  border: 2px solid #6F7278;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}

.ring-title {
  color: #6F7278;
  font-size: 0.75rem;
  position: absolute;
  top: -25px;
  margin: 0;
}

.ring.hovering {
  border-color: #3df1f1;
}

.ring-title.hovering {
  color: #3df1f1;
}