.mid {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
}

.switch {
  display: flex;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #888;
  overflow: hidden;
  padding: 10px;
}

.switch-left,
.switch-right {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.5rem;
  width: 3rem;
  background-color: #fff;
  border-radius: 0.15rem;
  color: #3df1f1;
}

.switch-left {
  margin-right: 0.5rem;
}